<template>
    <div class="news">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png">
            </span>
            <span class="usersTitle"></span>
            <span class="back"></span>
        </div>
        <div class="container">
            <div class="title">{{ article.fArticleTitle }}</div>
            <div class="details">
                <span class="time">
                    <span class="icon">
                        <img src="../assets/img/articleTime.png">
                    </span>
                    <span>{{ article.fArticleCover }}</span>
                </span>
                <span class="author">
                    <span class="icon">
                        <img src="../assets/img/articleAuthor.png">
                    </span>
                    <span>{{ article.fAuthor }}</span>
                </span>
            </div>
            <ul class="article">
                 <li v-for="(item, index) in article.fArticleCoverList" :key="index">
                    <span class="childTitle" v-if="item.fArticleSubheading !== null || ''">{{ item.fArticleSubheading }}</span>
                    <span class="content">{{ item.fArticleContent }}</span>
                    <span class="img" v-if="item.fArticleCover !== null || ''">
                        <img :src="item.fArticleCover">
                    </span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import store from '../vuex/store'
import { getNewsDetails } from '../api/getdata'
export default {
  name: 'news',
  data () {
    return {
      article: {}
    }
  },
  mounted () {
    console.log(store.state.articleId)
    this.getArticle()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getArticle () {
      let data = getNewsDetails({aId: store.state.articleId})
      data.then(res => {
        console.log(res)
        this.article = res.data
      })
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    font-size: 0.48rem;
    background: #fff;
    z-index: 99;
    top: 0;
    left: 0;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .container {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .title {
    margin-top: 1.5rem;
    text-align: start;
    font-size: 0.48rem;
    color: #333333;
  }

  .article {
    list-style: none;
    margin-top: 0.5rem;
  }

  .img {
    padding: 0.1rem 0;
  }

  .content {
    display: inline-block;
    width: 100%;
    font-size: 0.4rem;
    color: #666666;
    text-align: start;
    text-indent: 2em;
    line-height: 1.6;
    letter-spacing: 0.02rem;
  }

  .childTitle {
    display: inline-block;
    width: 100%;
    text-align: start;
    padding: 0.1rem 0;
  }

  .details {
    text-align: start;
    display: flex;
    margin-top: 0.37333rem;
  }

  .details span {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.34667rem;
    color: #666666;
  }

  .time span:last-child,
  .author span:last-child {
    margin-left: 0.16rem;
  }

  .author {
    margin-left: 0.42667rem;
  }

  .icon {
    display:flex;
    justify-content: center;
    align-items: center;
    width: 0.35rem;
    height: 0.35rem;
    border-radius: 50%;
  }
</style>



